import Store from "./store";
import router from "./router";

const apiCall = (type, path, body, callback) => {
	var xobj = new XMLHttpRequest();
	xobj.open(type, path, true);
	xobj.setRequestHeader("Content-Type", "application/json");
	xobj.setRequestHeader(
		"Authorization",
		`Bearer ${localStorage.getItem("access_token")}`
	);
	xobj.send(body);
	xobj.onreadystatechange = () => {
		if (xobj.readyState == 4 && xobj.status == "200") {
			callback(xobj.responseText);
		}
	};
};
export function setUserData() {
	const query = JSON.stringify({
		email: Store.state.userEmail,
	});
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/users/get_one",
		query,
		(e) => {
			let mongodata = JSON.parse(e);
			if (mongodata) {
				Store.state.userData = mongodata;
			} else {
				// no user present in DB with this email
				Store.state.userToBeCreated = true;
			}
		}
	);
}

export function setSelectedPlekje(id, callback) {
	getPlekje(id, (p) => {
		getTaggedIn(p.tags, (pcs) => {
			callback(pcs);
		});
	});
}

export function updateSelectedPlekje(pc, type) {
	let plekje = Store.state.selectedPlekje;

    // update plekje
	switch (type) {
		case "add":
			plekje.tags.push(pc._id);
			break;
		case "remove":
			plekje.tags = plekje.tags.filter(t => t != pc._id);
			break;
	}
	plekje.updatedAt = new Date().toISOString();

	updatePlekje(plekje._id, plekje, () => {});

	// update plekollection
	getPlekollection(pc._id, (plekollection) => {
        switch (type) {
            case "add":
                plekollection.plekjes.push({ _id: plekje._id, name: plekje.name });
                break;
            case "remove":
                plekollection.plekjes = plekollection.plekjes.filter(p => p._id != plekje._id);
                break;
        }
		plekollection.updatedAt = new Date().toISOString();
		updatePlekollectionData(plekollection._id, plekollection, () => {});
	});
}

const updatePlekje = (id, plekdata, callback) => {
	apiCall(
		"PUT",
		process.env.VUE_APP_API_URL + "/plekjes/update/" + id,
		JSON.stringify(plekdata),
		(e) => {
			callback();
		}
	);
};

export function getPlekjesWithoutTags(ids, callback) {
	const query = JSON.stringify({ ids: ids });
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/plekjes/get_no_tag/",
		query,
		(e) => {
			callback(JSON.parse(e));
		}
	);
}

const getTaggedIn = (tags, callback) => {
	const query = JSON.stringify({ ids: tags });
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/plekollections/get_multiple/",
		query,
		(e) => {
			let pcs = JSON.parse(e);
			if (pcs) {
				callback(pcs);
			} else {
				callback([]);
			}
		}
	);
};

export function setSelectedPlekollection(id, callback) {
	getPlekollection(id, (pc) => {
		Store.state.selectedPlekollection = pc;
		callback();
	});
}

export function setFeatureCollectionToEdit() {
	getFeatureCollection((fc) => {
		Store.state.featureCollectionToEdit = fc;
		router.replace("/");
	});
}

export function addSinglePlekjeToMap() {
	console.log(Store.state.selectedPlekje.name);
	if (
		!Store.state.featureCollections
			.map((x) => x.props.name)
			.includes(Store.state.selectedPlekje.name)
	) {
		Store.state.featureCollections.push(
			convertToFeatureCollection([Store.state.selectedPlekje], true)
		);
		router.replace("/");
	} else {
		router.replace("/");
	}
}

export function setFeatureCollectionToMap(callback) {
	if (
		!Store.state.featureCollections
			.map((x) => x.props.name)
			.includes(Store.state.selectedPlekollection.name)
	) {
		getFeatureCollection((fc) => {
			Store.state.featureCollections.push(fc);
			callback();
		});
	} else {
		router.replace("/");
	}
}

export function saveCreatedPlekollection(callback) {
	Store.state.creatingPlekollection = false;

    if (Store.state.featureCollectionToCreate.features.length == 1){
        savePlekjes(Store.state.featureCollectionToCreate.features,null,(plekjes)=>{
            updateUser(null,plekjes)
        })
    }
	if (Store.state.featureCollectionToCreate.features.length > 1) {
		let plekollection_name = prompt("Name Plekollection", " ");

		savePlekjes(
			Store.state.featureCollectionToCreate.features,
			plekollection_name,
			(plekjes) => {
				savePlekjesAsPlekollection(plekjes, plekollection_name);
				callback(true);
			}
		);
	} else {
		callback(false);
	}
}

export function editPlekollectionFlow(callback) {
	Store.state.editingPlekollection = false;
	let new_features = [];
	Store.state.featureCollectionToEdit.features.forEach((feature) => {
		// no id == new plekje
		if (!feature.properties.id) {
			new_features.push(feature);
		}
	});

	if (new_features.length > 0) {
		savePlekjes(
			new_features,
			Store.state.featureCollectionToEdit.props.name,
			(plekjes) => {
				updatePlekollection(plekjes, () => {
					callback();
				});
			}
		);
	} else {
		updatePlekollection(null, () => {
			callback();
		});
	}
}

export function checkUserNameAvailability(name, callback) {
	const query = JSON.stringify({ username: name });
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/users/name_check",
		query,
		(e) => {
			let userNameAvailable = e === "true";
			if (userNameAvailable) {
				registerUser(name);
			}
			callback(userNameAvailable);
		}
	);
}

const registerUser = (name) => {
	const newUser = {
		email: Store.state.userEmail,
		username: name,
		my_plekjes: [],
		my_plekollections: [],
	};
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/users/new",
		JSON.stringify(newUser),
		(e) => {
			Store.state.userToBeCreated = false;
			setUserData();
		}
	);
};

const getFeatureCollection = (callback) => {
	let ids = Store.state.selectedPlekollection.plekjes.map((x) => x._id);
	getPlekjesData(ids, (plekjes) => {
		callback(convertToFeatureCollection(plekjes));
	});
};

const getPlekjesData = (ids, callback) => {
	const query = JSON.stringify({ ids: ids });
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/plekjes/get_multiple/",
		query,
		(e) => {
			callback(JSON.parse(e));
		}
	);
};

const convertToFeatureCollection = (plekjes, isplekje) => {
	let geojson;
	if (isplekje) {
		geojson = {
			type: "FeatureCollection",
			props: {
				name: Store.state.selectedPlekje.name,
				id: Store.state.selectedPlekje._id,
				// in future more if you need them
			},
			features: [],
		};
	} else {
		geojson = {
			type: "FeatureCollection",
			props: {
				name: Store.state.selectedPlekollection.name,
				id: Store.state.selectedPlekollection._id,
				// in future more if you need them
			},
			features: [],
		};
	}

	plekjes.forEach((e) => {
		geojson.features.push({
			type: "Feature",
			properties: {
				name: e.name,
				id: e._id,
				// in future more if you need them
			},
			geometry: e.geometry,
		});
	});
	return geojson;
};

const savePlekjes = (features, featcol_name, callback) => {
	let new_plekjes = [];
	for (let i = 0; i < features.length; i++) {
		new_plekjes.push({
			name: features[i].properties.name,
			//description:"",
			tags: [],
			founder: Store.state.userData.username,
			//createdAt
			//updatedAt
			geometry: features[i].geometry,
		});
	}

	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/plekjes/add_multiple",
		JSON.stringify(new_plekjes),
		(e) => {
			callback(JSON.parse(e));
		}
	);
};

const savePlekjesAsPlekollection = (plekjes, name) => {
	let newPlekcollection = {
		name: name,
		//description:"",
		//tags: [],
		collector: Store.state.userData.username,
		//createdAt
		//updatedAt
		plekjes: plekjes,
	};
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/plekollections/new",
		JSON.stringify(newPlekcollection),
		(e) => {
			updateUser(JSON.parse(e), plekjes);
		}
	);
};

const updateUser = (plekollection, new_plekjes, plekjes_to_remove) => {
	const query = JSON.stringify({
		email: Store.state.userEmail,
	});
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/users/get_one",
		query,
		(e) => {
			let user = JSON.parse(e);

			// add new plekollection to my_plekollection
			if (plekollection) {
				user.my_plekollections.push({
					name: plekollection.name,
					_id: plekollection._id,
				});
			}
			// remove deleted plekjes
			if (plekjes_to_remove) {
				let ids_to_remove = plekjes_to_remove.map((x) => x._id);
				user.my_plekjes = user.my_plekjes.filter((x) => {
					return !ids_to_remove.includes(x._id);
				});
			}

			// add new plekjes to my_plekjes
			if (new_plekjes) {
				user.my_plekjes = user.my_plekjes.concat(new_plekjes);
			}

			// update user
			apiCall(
				"PUT",
				process.env.VUE_APP_API_URL + "/users/update/" + user._id,
				JSON.stringify(user),
				(e) => {
					setUserData();
				}
			);
		}
	);
};

const updatePlekollection = (new_plekjes, callback) => {
	getPlekollection(Store.state.featureCollectionToEdit.props.id, (pc) => {
		if (!pc) {
			return callback();
		} else {
			Store.state.selectedPlekollection = pc;
		}
		// check if plekollection needs to be updated
		let array1 = pc.plekjes.map((x) => x.name);
		let array2 = Store.state.featureCollectionToEdit.features.map(
			(x) => x.properties.name
		);
		if (array1.sort().join(",") === array2.sort().join(",")) {
			//dont update
			Store.state.editingPlekollection = false;
			// setFeatureCollectionToMap(()=>{});
		} else {
			// delete removed plekjes
			let ids_to_remain = Store.state.featureCollectionToEdit.features
				.map((x) => x.properties.id)
				.filter((x) => x);
			let plekjes_to_keep = [];
			let plekjes_to_remove = [];
			pc.plekjes.forEach((p) => {
				if (ids_to_remain.includes(p._id)) {
					plekjes_to_keep.push(p);
				} else {
					plekjes_to_remove.push(p);
				}
			});
			pc.plekjes = plekjes_to_keep;
			deletePlekjes(plekjes_to_remove.map((x) => x._id));

			// add new plekjes
			if (new_plekjes) {
				pc.plekjes = pc.plekjes.concat(new_plekjes);
			}
			pc.updatedAt = new Date().toISOString();

			updatePlekollectionData(
				Store.state.featureCollectionToEdit.props.id,
				pc,
				() => {
					updateUser(null, new_plekjes, plekjes_to_remove);
					Store.state.editingPlekollection = false;
					setSelectedPlekollection(
						Store.state.featureCollectionToEdit.props.id,
						() => {
							setFeatureCollectionToMap(() => {
								callback();
							});
						}
					);
				}
			);
		}
	});
};

const updatePlekollectionData = (id, pc_data, callback) => {
	apiCall(
		"PUT",
		process.env.VUE_APP_API_URL + "/plekollections/update/" + id,
		JSON.stringify(pc_data),
		(e) => {
			callback();
		}
	);
};

const deletePlekjes = (ids) => {
	apiCall(
		"DELETE",
		process.env.VUE_APP_API_URL + "/plekjes/delete_multiple",
		JSON.stringify({ ids: ids }),
		(e) => {}
	);
};

const getPlekollection = (id, callback) => {
	apiCall(
		"GET",
		process.env.VUE_APP_API_URL + "/plekollections/get_one/" + id,
		null,
		(e) => {
			if (e) {
				callback(JSON.parse(e));
			}
		}
	);
};

const getPlekje = (id, callback) => {
	apiCall(
		"GET",
		process.env.VUE_APP_API_URL + "/plekjes/get_one/" + id,
		null,
		(e) => {
			if (e) {
				let plekje = JSON.parse(e);
				Store.state.selectedPlekje = plekje;
				callback(plekje);
			}
		}
	);
};

